@import url(https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic);
* {
  margin: 0;
  padding: 0;
  color: initial;
  text-decoration: none;
  font-family: roboto;
  list-style-type: none;
}

body {
  height: 100vh;
}

main {
  height: 100%;
}

#navbar {
  height: 3em;
  display: flex;
  justify-content: right;
  align-items: center;
  gap: 1.5em;
  padding-right: 1.5em;
  font-size: medium;
  background-color: pink;
}
#navbar a:hover {
  color: palevioletred;
  transition: 0.1s;
}

footer {
  height: 3em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: smaller;
  background-color: pink;
}